// @flow
import React, { memo, useContext } from 'react';
import wordContext from '../../state/wordContext';
import { Icon, Modal } from '../../components';

import { WordLine, Heading, Button } from './style';
import icons from '../../assets/icons';
import FormChangeNumber from './FormChangeNumber';

const Homepage = () => {
  const { wordsToday, reload, updateStatusWord } = useContext(wordContext);
  return (
    <div style={{ padding: 0 }}>
      <Heading>
        <h2
          style={{
            fontSize: 30,
            fontWeight: 300
          }}
        >
          Words
        </h2>
        <div style={{ display: 'flex' }}>
          <Modal
            label={
              <Button>
                <Icon icon={icons.setting} size={15} />{' '}
              </Button>
            }
          >
            <FormChangeNumber />
          </Modal>
          <Button
            onClick={reload}
            role="presentation"
            style={{ marginLeft: 10 }}
          >
            <Icon icon={icons.reload} size={15} />{' '}
          </Button>
        </div>
      </Heading>

      {wordsToday.map((w, i) => (
        <WordLine
          onClick={() => updateStatusWord(w)}
          key={w.word}
          style={{ textDecoration: w.status ? 'line-through' : 'none' }}
        >
          <span>{i + 1}.</span>
          {w.word}
        </WordLine>
      ))}
    </div>
  );
};

export default memo(Homepage);
